import React from "react";

const Post = (props) => {
  return (
    <div>
      <span className="close"></span>
      <div className="postMyProfile">
        <div className="postMyProfileImg">
          <img
            src="https://static.1tv.ru/uploads/photo/image/2/huge/4062_huge_876c41f50e.jpg"
            alt="ava"
          />
        </div>
        <p>{props.name}</p>
        <p> {props.masege}</p>
      </div>
      <p>like{props.like}</p>
    </div>
  );
};
export default Post;
