import React from "react";
import MyPosts from "./MyPost/MyPosts";
import { add_post } from "./../../redux/state";

const MyProfile = (props) => {
   let newPostElement = React.createRef();

  let addPost = () => {
    let text = newPostElement.current.value;
    props.dispatch(add_post(text));
    newPostElement.current.value = '';
  };
  return (
    <div className="myProfileImg">
      <div>
        <img
          src="https://www.signivis.com/img/custom/avatars/member-avatar-05.png"
          alt="ava"
        />
      </div>
      <div className="addPost">
        <span>ADD POST</span>
        <textarea ref={newPostElement}  type="text"></textarea>
        <button onClick={addPost}>Add post</button>
      </div>
      <div>
        <MyPosts  PostTable={props.PostTable} />
      </div>
    </div>
  );
};
export default MyProfile;
