import ProfileReduser from './ProfileReduser'
import MesegeReduser from './MesegeReduser'


let store = {
  state: {
    Post: {
      PostTable: [
        { name: "Ruslan", masege: "he", like: 23 },
        { name: "Ivan", masege: "heeee", like: 2133 },
        { name: "Petro", masege: "how are you", like: 443 },
      ],
    },
    Person: {
      PersonImport: [
        { id: 1, name: "Ruslan", lastName: "Dnistrianskui" },
        { id: 2, name: "Rus", lastName: "Dnistrjjnkj" },
        { id: 3, name: "Stepan", lastName: "Dnis" },
        { id: 4, name: "Ivan", lastName: "Dnis" },
      ],
      importMasege: [
        { id: 1, Masege: "He" },
        { id: 2, Masege: "He how are you" },
        { id: 3, Masege: "He" },
      ],
      createPosts:[ ''],
    },
  },
  reRender() {},

  dispatch(action) {

    this.state.Post = ProfileReduser(this.state.Post, action);
    this.state.Person = MesegeReduser(this.state.Person, action);
    this.reRender(this.state);

    // if (action.type === "ADD-MESEGE") {
    //   let newMesege = { id: 7, Masege: action.postMesege };
    //   this.state.Person.importMasege.push(newMesege);
    //   this.reRender(this.state);
    // } else if (action.type === "ADD-POST") {
    //   let newPost = {
    //     name: "Petro",
    //     masege: action.postMessage,
    //     like: 0,
    //   };
    //   this.state.Post.PostTable.push(newPost);
    //   this.reRender(this.state);
    // }
  },

  addMesege(postMesege) {
    let newMesege = { id: 7, Masege: postMesege };
    this.state.Person.importMasege.push(newMesege);
    this.reRender(this.state);
  },
  addPost(postMessage) {
    let newPost = {
      name: "Petro",
      masege: postMessage,
      like: 0,
    };
    this.state.Post.PostTable.push(newPost);
    this.reRender(this.state);
  },
  subscribes(observer) {
    this.reRender = observer;
  },
};

export const add_post = (text) => ({ type: "ADD-POST", postMessage: text });


export const add_mesege = (masege) => ({
  type: "ADD-MESEGE",
  postMesege: masege,
});

export default store;
