import React from "react";
import { add_mesege } from "./../../../redux/state";
import userPhoto from "./../../../photos/user.png";

const Masege = (props) => {
  return (
    <p id={props.id}>
      <img
        className="photoUrlMesege"
        src={props.photo}
        alt=""
      />{" "}
      {props.Masege}
    </p>
  );
};

const PersonMasege = (props) => {
  let addMasegeComponent = React.createRef();
  let Add_Masege = () => {
    let masege = addMasegeComponent.current.value;
    debugger;
    props.dispatch(add_mesege(masege));
    addMasegeComponent.current.value = "";
  };

  const HereMasege = props.importMasege.map((p) => (
    <Masege id={p.id} photo={p.photo != null ? p.photo : userPhoto} key={p.id} Masege={p.Masege} />
  ));
  debugger
  return (
    <div>
      <input ref={addMasegeComponent} type="text" />
      <button onClick={Add_Masege}>Add Masege</button>
      {HereMasege}
    </div>
  );
};
export default PersonMasege;
