import React from "react";
import MyProfile from "../navigator/MyProfile/MyProfile";
import { Route } from "react-router";
import MyMaseges from "../navigator/MyMaseges/MyMaseges";
import Setting from "../navigator/Setting/Setting";
import MyImages from "../navigator/MyImage/MyImages";
import MyMusic from "../navigator/MyMusic/MyMusic";

const Content = (props) => {
  return (
    <div className="content">
      <Route
        path="/MyProfile"
        render={() => (
          <MyProfile
            dispatch={props.dispatch}
            PostTable={props.state.Post.PostTable}
          />
        )}
      />
      <Route
        path="/MyMaseges"
        render={() => (
          <MyMaseges dispatch={props.dispatch} state={props.state} />
        )}
      />
      <Route path="/MyImages" render={() => <MyImages />} />
      <Route path="/MyMusic" render={() => <MyMusic />} />
      <Route path="/Setting" component={Setting} />
    </div>
  );
};

export default Content;
