let initialState = {
  PostTable: [
    { name: "Ruslan", masege: "he", like: 23, id: 1 },
    { name: "Ivan", masege: "heeee", like: 2133, id: 2 },
    { name: "Petro", masege: "how are you", like: 443, id: 3 },
  ],
};

const ProfileReduser = (state = initialState, action) => {
  switch (action.type){
  case "ADD-POST" : 
    let body = action.postMessage
    return {
      ...state,
      PostTable: [
        ...state.PostTable,
        {
          name: "Petro",
          masege: body,
          like: 0,
        },
      ],
    }
    default:
      return state;
    // let newPost = {
    //   name: "Petro",
    //   masege: action.postMessage,
    //   like: 0,
    // };

    // stateCopy.PostTable.push(newPost);
  }

};
export default ProfileReduser;
