import Person from "./Person/Person";
import PersonMasege from "./Person/PersonMasege";

const MyMaseges = (props) => {
  return (
    <div className="masegePerson">
      <div>
        <Person PersonImport={props.state.Person.PersonImport} />
      </div>
      <div>
        <PersonMasege
          dispatch={props.dispatch}
          importMasege={props.state.Person.importMasege}
        />
      </div>
    </div>
  );
};
export default MyMaseges;
