import React from "react";
import { NavLink } from "react-router-dom";

const Nav = () => {
    return(
        <div className='navigator'>
            <div><NavLink to='/MyProfile'> My Profile</NavLink></div>
            <div><NavLink to='/MyMaseges'> My Maseges</NavLink></div>
            <div><NavLink to='/MyMusic'>My Music</NavLink></div>
            <div><NavLink to='/MyImages' >My Images</NavLink></div>
            <div className='setting'><NavLink to='/Setting'>Setting</NavLink></div>
            
        </div>
    )
}
export default Nav;