import React from "react";
import { NavLink } from "react-router-dom";

const PersonSet = (props) => {
  let path = "/MyMaseges/" + props.id;
  return (
    <div className='PersonHereIS'>
      <NavLink to={path}>
        {props.name} {props.lastName}
      </NavLink>
    </div>
  );
};

const Person = (props) => {
  let PersonHere = props.PersonImport.map((p) => (
    <PersonSet id={p.id} name={p.name} key = {p.id} lastName={p.lastName} />
  ));
  return <div className='personHere'>{PersonHere}</div>;
};
export default Person;
