let initialState = {
  PersonImport: [
    { id: 1, name: "Ruslan", lastName: "Dnistrianskui", },
    { id: 2, name: "Rus", lastName: "Dnistrjjnkj" },
    { id: 3, name: "Stepan", lastName: "Dnis" },
    { id: 4, name: "Ivan", lastName: "Dnis" },
  ],
  importMasege: [
    { id: 1, Masege: "He", photo: 'https://www.pngall.com/wp-content/uploads/5/User-Profile-PNG-HD-Image.png' } ,
    { id: 2, Masege: "He how are you" },
    { id: 3, Masege: "He" },
  ],
};

const MesegeReduser = (state = initialState, action) => {
  switch (action.type) {
    case "ADD-MESEGE":
    return {...state,
      importMasege: [...state.importMasege, { id: 7, Masege: action.postMesege } ],
    };
    // let newMesege = { id: 7, Masege: action.postMesege };
    // stateCopy.importMasege.push(newMesege);
    default:
      return state
  }
 
};

export default MesegeReduser;
